<template>
  <div class="about">
    <iframe class="frame_style" :src="'/pdfjs/web/viewer.html?file=' + filePath">
    </iframe>
  </div>
</template>
<script>
export default {
  data () {
    return {
      title: '',
      filePath: '/pdf/algorithm.pdf'
    }
  },
  mounted () {
    this.getBookInfo()
  },
  methods: {
    getBookInfo () {
      this.title = this.$route.params.title
      this.$http.get('/books/' + this.title).then(res => {
        console.log(res.data)
        this.filePath = res.data.data.filePath
      })
    }
  }
}
</script>
<style scoped>
.frame_style{
  width: 100%;
  height: 700px;
}
</style>
